.app-gameoflife {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
    font-family: 'Inconsolata';
    .rules {
        display: flex;
        flex-direction: column;
        align-items: center;
        .button {
            color: white;
            border: white 1px solid;
            padding: 4px 10px;
            text-align: center;
            border-radius: 10px;
            margin-top: 1rem;
            &:hover {
                cursor: pointer;
            }
        }
        .rules-content {
            color: white;
            margin: auto;
            margin-top: 1rem;
            max-width: 50%;
            font-size: 14px;
            background-color: rgba($color: #000000, $alpha: 0.7);
            padding: 5px 10px;
        }
    }

    .board {
        line-height: 0;
        margin: auto;
        margin-top: 20px;
    }
    .box {
        display: inline-block;
        border: 1px dotted #ccc;
        width: 10px;
        height: 10px;
        margin-left: -1px;
        margin-bottom: -1px;
        &.off {
            background-color: rgba($color: #000000, $alpha: 0.6);
        }

        &.on {
            background-color: white;
            border-radius: 50%;
        }

        &:hover {
            background: #00ccff;
            cursor: pointer;
        }
    }

    .buttons {
        display: flex;
        justify-content: center;
        margin-top: 10px;
        button {
            background: #555;
            color: white;
            font-size: 14px;
            padding: 8px 16px;
            border: white 1px solid;
            &:hover {
                cursor: pointer;
                opacity: 0.8;
            }
            &:active {
                opacity: 0.8;
            }
        }
    }

    .patterns {
        display: grid;
        max-width: 60%;
        grid-template-columns: repeat(4, 1fr);
        margin: 20px auto;
        justify-content: center;
        .help {
            color: lightblue;
        }
        .pattern-container {
            width: 100%;
            padding: auto;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .pattern {
                line-height: 0;
            }
        }
    }

    .center {
        margin: auto;
        display: table;
        margin-top: 10px;
    }

    h1,
    h2,
    h3,
    h4,
    h5 {
        color: #fafafa;
        margin: auto;
        text-align: center;
        width: 50%;
        margin-top: 10px;
    }
}
