// Media Query Mixins
@mixin mediaXs {
    @media screen and (max-width: 400px) {
        .greeting {
            width: 300px;
            font-size: 2.5rem;
        }
        .console {
            width: 90%;
            .skills {
                .skill {
                    height: 40px;
                    width: 40px;
                    img {
                        height: 40px;
                    }
                }
            }
        }
    }
}

@mixin mediaSm {
    @media screen and (max-width: 500px) {
        .main-nav {
            font-size: 1rem;
            .nav-items {
                width: 80%;
            }
        }
        .console {
            width: 90%;
            height: fit-content;
            .skills {
                width: 100%;
                .skill {
                    height: 60px;
                    width: 60px;
                    img {
                        height: 50px;
                    }
                }
            }
        }
        .cards {
            flex-direction: column;
            width: 90%;
            height: 420px;
            .card {
                width: 100%;
                height: 150px;
                display: flex;
                margin: 0.5rem 0;
                img {
                    width: 50%;
                    height: 100%;
                }
                .container {
                    width: 50%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: space-evenly;
                    .description {
                        display: none;
                    }
                    .link {
                        position: initial;
                    }
                }
            }
        }
    }
}

@mixin mediaMd {
    @media screen and (max-width: 768px) {
        .cards {
            .card {
                .description {
                    font-size: 0.8rem;
                }
            }
        }
    }
}
