@import 'config';
@import 'gameoflife';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: url('../img/background.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    font-family: 'Roboto Mono', monospace, Arial, Helvetica, sans-serif;
}

.main {
    margin: 0 auto;
    margin-top: 70px;
    max-width: 1100px;
    height: 100vh;
}

.main-nav {
    height: 50px;
    width: 100%;
    position: fixed;
    top: 0;
    font-size: 1.2rem;
    font-family: 'Russo One', monospace;
    color: #ccc;
    background-color: #232329;
    letter-spacing: 2px;
    z-index: 1;
    .nav-items {
        height: 100%;
        width: 50%;
        margin: 0 auto;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        list-style: none;
        .nav-item {
            cursor: pointer;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.about .project .contact {
    height: 100vh;
}

.greeting {
    width: 400px;
    margin: 1rem auto;
    padding: 10px 0;
    color: white;
    font-size: 3rem;
    text-align: center;
}

.console {
    border: 1px solid #f1f1f1;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    width: 80%;
    height: 60vh;
    margin: 0 auto;
    background: rgba($color: #000000, $alpha: 0.7);
    color: #00ff0a;
    font-family: 'Inconsolata', 'Roboto Mono', monospace;
    position: relative;
    .bar {
        padding: 4px;
        background: #f1f1f1;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        &:after {
            content: '';
            display: table;
            clear: both;
        }
        .dots {
            float: left;
            height: 100%;
            .dot {
                margin: auto 3px;
                height: 12px;
                width: 12px;
                background-color: #bbb;
                border-radius: 50%;
                display: inline-block;
                &.red {
                    background: #ed594a;
                }
                &.yellow {
                    background: #fdd800;
                }
                &.green {
                    background: #5ac05a;
                }
            }
        }
    }
    .content {
        height: 30%;
        width: 100%;
        padding-left: 5px;
    }
    .skills {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        height: 50%;
        width: 80%;
        margin-top: 1.5rem;
        .skill {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 80px;
            width: 80px;
            margin: 10px;
            font-family: 'Russo One';
            color: #cccccc;
            img {
                height: 60px;
                background-size: contain;
                background-position: center;
            }
        }
    }
}

.lead {
    width: 90%;
    margin: 1rem auto;
    margin-top: 150px;
    padding: 5px 20px;
    border-left: solid 4px white;
    color: white;
    background: rgba($color: #000000, $alpha: 0.2);
    font-size: 1.5rem;
    font-family: 'Russo One';
}

.cards {
    width: 90%;
    height: 420px;
    display: flex;
    margin: 2rem auto;
    position: relative;
    .card {
        width: 30%;
        height: 100%;
        background-color: #fff;
        margin: 0 auto;
        border-radius: 10px;
        position: relative;
        img {
            width: 100%;
            height: 40%;
            background-size: contain;
        }
        .name {
            font-family: 'Russo One';
            font-size: 1.3rem;
            margin-left: 0.5rem;
            margin-bottom: 0.5rem;
        }
        .tag {
            width: fit-content;
            padding: 4px 20px;
            border-radius: 15px;
            font-size: 0.8rem;
            font-weight: bolder;
            text-align: center;
            margin-left: 0.5rem;
            margin-top: 0.5rem;
            margin-bottom: 0.5rem;
        }
        .description {
            margin: 0 0.5rem;
        }

        .link {
            position: absolute;
            bottom: 1rem;
            width: 100%;
        }

        .btn {
            font-family: 'Inconsolata', sans-serif;
            text-decoration: none;
            color: white;
            background: #232329;
            padding: 6px 10px;
            width: fit-content;
            margin-left: 0.5rem;
            display: flex;
            align-items: center;
            i {
                margin-right: 5px;
            }
        }
    }
}

.contact {
    color: white;
    font-size: 1.1rem;
    ul {
        width: 90%;
        margin: 0 auto;
        margin-bottom: 3rem;
        padding-left: 1rem;
        list-style: none;
    }
}

@include mediaXs;
@include mediaSm;
@include mediaMd;
